import check from '@/static/mbcImg/home/extendManageList/dataReport/check.png';
import checked from '@/static/mbcImg/home/extendManageList/dataReport/checked.png';
import checkedItems from '@/static/mbcImg/home/extendManageList/dataReport/checkedItems.png';
import selectEd from '@/static/mbcImg/publish/createBusiness/selectEd.png';
import select from '@/static/mbcImg/publish/createBusiness/select.png';
export default {
	name: 'businessPutIn',
	props: [
		'disabled', 
		'dataList',
		'dataListselectList',
		'type',
		'dataType'
	],
	data() {
		return {
			dataLists: [], // 城防多选的转型后数组
			selcetDataList: [], // 盛放选中的商机
			check: check, // type 选择按钮的未选中状态 img
			checked: checked, // type 选择按钮的选中状态 img
			checkedItems: checkedItems, // 子项选中对勾 img
			selectEd: selectEd, // 勾选圆框 黑色
			select: select, // 未勾选圆框 白色
			clickItemsIndex: '', // 记录子项点击的index
			selectStyle2: { // 选择之后样式边框变化
				background: '#02C2A2',
				color: '#FFFF',
			},
			noSelectStyle2:  { // 选择之前样式边框变化
				color: '#2E2E30',
			},
		};
	},
	components: {},
	computed: {},
	watch: {},
	created() {
		this.resetData(this.dataList);
	},
	watch: {
		dataList: {
			handler(a, b) {
				console.log(a, b, '--------------------------------------dataList---------------------------------------');
				this.resetData(a);
			},
			deep: true
		}
	},
	mounted() {},
	methods: {
		// 重置数组数据，符合多选格式
		resetData(e) {
			this.dataLists = [];
			let checkData = []; // 组件返回值
			e.map((items, index)=>{
				if (this.dataType === '2') {
					let item = {
						selectType: items.selectType,
						type: items.type,
						typeName: items.typeName,
						key: items.key,
						value: items.value,
						checked: items.checked ? items.checked : false
					};
					// 实例保障和支付方式的双向保定
					if (this.dataListselectList) {
						this.dataListselectList.map((iteme, inde)=>{
							if (iteme.key === items.key && this.dataType === items.type) {
								item.checked = true;
								checkData.push(item);
								console.log(checkData, '---------------checkData------------')
								this.$emit('tap-CheckBox', checkData); // 将用户选择下来的子项返给前台
							}
						});
					}
					this.dataLists.push(item);
				} else if (this.dataType === '3') {
					console.log('图片选择');
					let item = {
						imgUrl: items.imgUrl,
						title: items.title,
						select: items.select,
						value: items.title,
						checked: items.select
					};
					this.dataLists.push(item);
				} else if (this.dataType === '4') {
					console.log('样式选择！');
					let item = {
						imgUrl: items.imgUrl,
						title: items.title,
						content3: items.content3,
						select: items.select,
						value: items.title,
						checked: items.select
					};
					this.dataLists.push(item);
				} else {
					let item = {
						value: items.value ? items.value : items,
						checked: false
					};
					this.dataLists.push(item);
				}
				console.log(this.dataLists, 'this.dataLists')
			})
		},
		// 勾选商机
		labelBtn(name, index) {
			if (this.selcetDataList.join(',').indexOf(name) > -1) {
				this.selcetDataList.map((items, key) => {
					this.dataLists[index].checked = true;
				});
			} else {
				this.dataLists[index].checked = false;
			}
		},
		checkboxChange: function(e) {
			this.selcetDataList = e.detail.value; // 获取选中的值
			console.log(this.selcetDataList, '--------------------selcetDataList-----------------------');
			let checkData = []; // 组件返回值
			
			if (this.dataType === '3' || this.dataType === '4') {
				console.log('图片选择');
				let data=[];
				console.log(JSON.stringify(this.dataLists))
				console.log(this.dataLists, '--------------this.dataLists----------------')
				this.dataLists.map((items, index)=>{
					let dataItems={
						imgUrl: items.imgUrl,
						select: false,
						title: items.title
					};
					if (this.dataType === '4') {
						dataItems.content3 = items.content3
					};
					this.selcetDataList.map((item,key)=>{
						console.log(items.title, item, '----------------items.title-------------')
						if (items.title === item){
							dataItems.select = true;
						}
					})
					data.push(dataItems);
				});
				this.$emit('tap-CheckBox', data); // 将用户选择下来的子项返给前台
			} else {
				this.dataLists.map((items, index)=>{
					this.selcetDataList.map((item,key)=>{
						if (items.value === item){
							checkData.push(items);
						}
					})
				});
				this.$emit('tap-CheckBox', checkData); // 将用户选择下来的子项返给前台
			}
		}
	}
};
